'use client';

import { useEffect, useState, useCallback } from 'react';
import {
  Box,
  TextField,
  Alert,
  CircularProgress,
  
} from '@mui/material';
import { MuiTelInput } from 'mui-tel-input';

import useUserProfile from '@/hooks/useUserProfile';
import Header from '@/components/ui/containers/Header';
import BackButton from '@/components/ui/buttons/BackButton';
import PrimaryButton from '@/components/ui/buttons/PrimaryButton';

/* ------------------------------------------------------------------ */
/* small local hook – handles form state & dirty flag                 */
/* ------------------------------------------------------------------ */
function useProfileForm(initial = { name: '', phone: '', address: '' }) {
  const [fields, set] = useState(initial);
  const [dirty, setDirty] = useState(false);

  // when the server user arrives → reset form & clear dirty
  useEffect(() => {
    set(initial);
    setDirty(false);
  }, [initial.name, initial.phone, initial.address]);

  const update = useCallback(
    <K extends keyof typeof fields>(k: K, v: (typeof fields)[K]) => {
      set(f => ({ ...f, [k]: v }));
      setDirty(true);
    },
    []
  );

  return { fields, update, dirty, resetDirty: () => setDirty(false) };
}

/* ------------------------------------------------------------------ */
/* main component                                                     */
/* ------------------------------------------------------------------ */
export default function ProfileManagement() {
  const { user, isLoading } = useUserProfile();

  const {
    fields: form,
    update,
    dirty,
    resetDirty,
  } = useProfileForm({
    name: user?.name ?? '',
    phone: user?.phone ?? '',
    address: user?.address ?? '',
  });



  

  /* ---------------------------------------------------------------- */

  if (isLoading || !user)
    return (
      <Box className="flex justify-center py-20">
        <CircularProgress />
      </Box>
    );

  return (
    <Box className="max-w-lg mx-auto px-4 pb-20 min-h-[90vh] flex flex-col">
      <Header
        left={<BackButton smartNavigation />}
        center={<h1 className="text-lg font-semibold">My Profile</h1>}
      />

    
      

  

    </Box>
  );
}
